import React from 'react';
import ScheduleDetailsBannerComponent from './schedule-details-banner/ScheduleDetailsBannerComponent';

function ScheduleDetailsComponent() {
  return (
    <div>
      <ScheduleDetailsBannerComponent componentName="Generated Schedules" />

      <hr style={{ background: '#c8c8c8' }} />

      <div className="d-flex">
        <div style={{ width: '100%' }} />
      </div>
    </div>
  );
}

export default ScheduleDetailsComponent;
