import React from 'react';
import PropTypes from 'prop-types';
import Styles from './ScheduleDetailsBanner.module.css';

function ScheduleDetailsBannerComponent({ componentName }) {
  return (
    <div className={`${Styles.header} position-relative`}>
      <div className={Styles.headerText}>{componentName}</div>
      <div className="d-flex position-absolute end-0" style={{ bottom: 0 }}>
        <div className="row">
          <div className={Styles.labelText}>
            Channel:
            {' '}
            <b>Sky</b>
          </div>

          <div className={Styles.labelText}>
            Month:
            {' '}
            <b>February</b>
          </div>

          <div className={Styles.labelText}>
            Year:
            {' '}
            <b>2020</b>
          </div>
        </div>
      </div>
    </div>
  );
}
ScheduleDetailsBannerComponent.propTypes = {
  componentName: PropTypes.string.isRequired,
};
export default ScheduleDetailsBannerComponent;
